.NavBar{
  display: flex;
  justify-content: space-around;
  padding: 22px;
}
.right{
  display: flex;
  justify-content: space-around;
  
}
.logo{
  font-size: 20px;
}
.cart{
  display: flex;
  margin: 0px;
  margin-left: 20px;
  
}
h5{
  margin: 0px;
  font-size: 19px;
  color: #666;
  transition: border-bottom 0.1s;
}
h5:hover{
  border-bottom: solid red;
}
.img{
  height: 25px;
}
* {
  box-sizing: border-box;
}

/* Style the search field */
form.example input[type=text] {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  background: #f1f1f1;
  height: 30px;
  border-radius: 12px 0px 0px 12px ;
  border-color:#ffda00 ;
}

/* Style the submit button */
form.example button {
 
  height: 30px;
  float: left;
  width: 20%;
  padding: 10px;
  background: #ffda00;
  color: white;
  font-size: 10px;
  border: 1px solid grey;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
  border-radius: 0px 12px 12px 0px ;
  border-color:#ffda00;
 
}




/* Clear floats */
form.example::after {
  content: "";
  clear: both;
  display: table;
}
 

